
import React, {useState} from 'react';
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import { useHistory } from "react-router-dom";

import { post } from "../components/DataProvider";


export const Login = function (props) {
    
    let history = useHistory();
    const [editFormData, setEditFormData] = useState({});
    const [validated, setValidated] = useState(false);

    const handleInputsChange = (event) =>{
        let key = event.target.name
        let value = event.target.value
        let formData = { ...editFormData }
        formData[key] = value
        setEditFormData({...formData})
    }

    const submitForm = async (event) => {
        event.preventDefault()

        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        }

        setValidated(true);
        if (!form.checkValidity()) {
            return
        }
        let updateData = { ...editFormData }
        
       
        let response = await post(`/login`, updateData)
        //history.push(`/${props.resource}`)
        if (response && !response.errorMessage) {
            history.push('/buyers')
        }
        
    }
    
    return (
        <>
            <Container >
                <Row >
                    <Col md={{ span: 6, offset: 3 }}>
                        <div>
                            <img src="logo-invoice.png" style={{width:'400px', height:'200px'}} />
                        </div>
                        <Form noValidate validated={validated} onSubmit={submitForm}>
                        <Form.Group className="mb-3" >
                            <Form.Label>User Name</Form.Label>
                                <Form.Control
                                    name="userName"
                                    type="text"
                                    onChange={handleInputsChange}
                                />

                            <Form.Control.Feedback type="invalid" >
                                {`Please enter a username` }
                            </Form.Control.Feedback>
                        </Form.Group>
                        
                        <Form.Group className="mb-3" >
                            <Form.Label>Password</Form.Label>
                                <Form.Control
                                    name="password"
                                    type="password"
                                    onChange={handleInputsChange}
                                />

                            <Form.Control.Feedback type="invalid" >
                                {`Please enter a password` }
                            </Form.Control.Feedback>
                            </Form.Group>
                            
                            <Button className="submit-button" variant="primary" type="submit" >
                            Login
                        </Button>
                    </Form>
                    </Col>

                </Row>
        </Container>
        </>
    )
}